nav{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    position: sticky;  
    z-index: 1;
  }
.logo{
    color: white;
    font-size: 50px;
}
nav ul{
    display: flex;
    padding: 20px 30px;
}
nav ul a{
    text-decoration: none;
    padding: 5px 12px;
    cursor: pointer;
}
.res{
  display: block;
}
nav ul a:hover{
    border-bottom: 1px solid blue;
}



.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
  }
  
  .sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
  }
  
  .sidenav a:hover {
    color: #f1f1f1;
  }
  
  .sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
  }
  .span-res{
    font-size:30px;
    cursor:pointer;
    display: none;
    color: white;
  }
  @media screen and (max-width: 450px) {
    .span-res{
      display: block;
    }
    .res{
      display: none;
    }
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }